<template lang="html">

  <div id="header">
    <div class="swiper-wrapper">
      <div class="swiper-slide li" v-for="(item,index) in navs"
           @click="isActive(index)" :class="chooseId==index?'is-selected':''">{{item}}</div>
    </div>
  </div>
</template>
<style type="text/css" scoped>
  @import url("https://cdn.bootcss.com/Swiper/4.0.1/css/swiper.min.css");
  .swiper-slide.li{ display: inline-block; padding: 0 20px; width: auto}

</style>
<script>
  import $ from 'jquery'
  import Swiper from '../../static/js/swiper.min.js'
  export default {
    data () {
      return {
        chooseId: '0',
      }
    },
    props: {
      navs: Array,
      chooseItem: Number
    },
    computed:{
      fontSize(){
//        console.log(this.navs)
        return "15px"
      }
    },
    methods: {
      isActive (index) {
        this.chooseId = index
        // 利用vuex 来进行状态管理 不适用emit
        // this.$emit('selectNav', index)
        this.$store.state.navState = this.chooseId
      }
    },
    created () {
      this.$nextTick(() => {
        var mySwiper1 = new Swiper('#header', {
          freeMode: true,
          slidesPerView: 'auto',
        });
      })
    },
    watch: {
      "chooseItem" () {
        this.chooseId = this.chooseItem-1
      }
    },


  }
</script>



