<template>
  <div class="body"  ref="contentWrapper">
    <div class="content-scroll">
      <div class="content">
        <p>Welcome home</p>
        <currency-model :pData="pData" @topMenu="topMenu"></currency-model>

        <div style="padding: 50px 0 0 0">
          <navbar :navs="navs" :chooseItem="chooseItem"></navbar>
        </div>
        <p style="padding-top: 400px; padding-bottom: 50px">Welcome home</p>
      </div>
    </div>
  </div>
</template>
<style>

</style>
<script>
  import  { Toast }  from 'mint-ui'
  import BScroll from 'better-scroll'
  import currencyModel from '../components/currency.vue'
  import navbar from '@/components/NavBar.vue'
  export default {
    data(){
      return{
        navs: ['特卖','极速免税店','母婴','轻奢','服饰','鞋包','食品','极速免税店','母婴','轻奢','服饰','鞋包','食品'],
        chooseItem: 0,
        pData:"我是父组建数据",
        watchNanme:"old"
      }
    },
    components: {
      currencyModel,
      navbar
    },
    computed: {
      getNavState () {
        return this.$store.state.navState
      }
    },
    watch: {
      getNavState(state,oldVal){
        var index = state + 1
        console.log("newVal:"+state)
        console.log("oldVal:"+oldVal)
        // 跳转子页面 this.$router.push()
       this.$router.push('/Props')
      },
      watchNanme(val,oldVal){
          console.log(val,oldVal)
      }

    },
    methods: {
      topMenu (data) {
        console.log("子组建传来数据："+data)
      },
      _initScroll () {
        this.typeScroll = new BScroll(this.$refs.contentWrapper, {
          click: true,
          probeType: 3
        })
      }
    },
    mounted(){
//      this.$on("topMenu", function (a) {
//        console.log("data",a)
//      });
    },
    created(){
      console.log("routeName:"+this.$route.name)

      var _this=this;
      setTimeout(function () {
        Toast({
          message: '操作成功',
          iconClass: 'icon icon-success'
        });
        _this.watchNanme='new';
      },3000)

      console.log("state:"+this.$store.state.navState)


      this.$nextTick( () => {
        this._initScroll()
      })

      this.$emit("navType","1")
    }
  }
</script>



