<template>
  <div class="footer">
    <h1><span style="font-size: 18px; color: red">props:{{pData}}</span></h1>
  </div>
</template>
<script>

  export default {
    props:['pData'],
    data () {
      return {
        name: '哈哈哈'
      }
    },
    methods:{
      transmit:function () {
        this.$emit("topMenu","我是子组建数据")
      }
    },
    created:function () {
      this.transmit()
      console.log(this.pData)//获取props数据
    }
  }
</script>

<style scoped>
  h1 {
    color: cornflowerblue;
  }
</style>



