import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home.vue'
import Page2 from '@/pages/Page2.vue'
import Swiper from '@/pages/Swiper.vue'
import Props from '@/pages/Props.vue'
import Mint from '@/pages/Mint.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Home'
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    {
      path: '/Page2',
      name: 'Page2',
      component: Page2
    },
    {
      path: '/Swiper',
      name: 'Swiper',
      component: Swiper
    },
    {
      path: '/Props',
      name: 'Props',
      component: Props
    },
    {
      path: '/Mint',
      name: 'Mint',
      component: Mint
    }
  ]
})



