<template>
  <div class="body"  ref="contentWrapper">
    <div class="content-scroll">
      <div class="content">
        <p class="name">Welcome Page2页面2</p>
        {{ baidu.desc }}
        <table  class="table table-bordered">
          <tr>
            <td>1</td>
            <td rowspan="2">1</td>
          </tr>
          <tr>
            <td>1</td>
          </tr>
        </table>
        <currency-model :pData="test" ></currency-model>
      </div>
    </div>
  </div>
</template>
<style type="text/css">

</style>
<script>
  import BScroll from 'better-scroll'
  import currencyModel from '../components/currency.vue'
  import $ from 'jquery'
  export default {
    components: {
      currencyModel
    },
    data () {
      return {
        test:"page2父数据",
        baidu: 'Welcome to Your Vue.js App'
      }
    },
    methods: {
      GetData: function () {
        var that = this;
        $.getJSON("/baidu/api/openapi/BaikeLemmaCardApi?scope=103&format=json&appid=379020&bk_key=%E9%93%B6%E9%AD%82&bk_length=600").then(function (data) {
          console.log(data)
          that.baidu=data;
        });


      },
      _initScroll () {
        this.typeScroll = new BScroll(this.$refs.contentWrapper, {
          click: true,
          probeType: 3
        })
      }
    },
    created: function () {
      this.$nextTick( () => {
        this._initScroll()
      })
      this.$emit("navType","2")
      this.GetData();

    }
  }
</script>





