<template>
  <div class="body"  ref="contentWrapper">
    <div class="content-scroll">
      <div class="content">
        <p>Props</p>
        <child v-for= "item1 in poem" :key="item1.id" :message="item1"></child>
        <div style="height: 1000px; background-color: gray"></div>
      </div>
    </div>

  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import child from '../components/example.vue'
  export default {
    data(){
      return{
        poem:[{message:'山有木兮木有枝'},{message:'心悦君兮君不知'}]
      }
    },
    components: {
      child
    },
    methods: {
      _initScroll () {
        this.typeScroll = new BScroll(this.$refs.contentWrapper, {
          click: true,
          probeType: 3
        })
      }
    },
    created(){
      this.$nextTick( () => {
        this._initScroll()
      })
      this.$emit("navType","4")
    }
  }
</script>



