<template>
  <div class="body"  ref="contentWrapper">
    <div class="content-scroll">
      <div class="content">
        <div class="sw" style="height: 100px">
          <mt-swipe :showIndicators="false">
            <mt-swipe-item>菜单1</mt-swipe-item>
            <mt-swipe-item>菜单2</mt-swipe-item>
            <mt-swipe-item>菜单3</mt-swipe-item>
          </mt-swipe>
        </div>

        <mt-radio align="left" title="单选框列表" v-model="value" :options=options></mt-radio>
      </div>
    </div>
  </div>
</template>
<style type="text/css">
.mint-swipe-item{ background: gray}
</style>
<script>
  import { Radio,MessageBox,Indicator,Swipe, SwipeItem  } from 'mint-ui';
  import BScroll from 'better-scroll'
  import $ from 'jquery'
  export default {
    components: {
      Radio,
      Swipe,
      SwipeItem
    },
    data () {
      return {
        value:"选项A",
        options:[
          {
            label: '被禁用',
            value: '值F',
            disabled: true
          },
          {
            label: '选项A',
            value: '值A'
          }
        ]
      }
    },
    methods: {

      _initScroll () {
        this.typeScroll = new BScroll(this.$refs.contentWrapper, {
          click: true,
          probeType: 3
        })
      }
    },
    created: function () {
//      Indicator.open({
//        text: '加载中...',
//        spinnerType: 'fading-circle'
//      });
      //MessageBox('提示', '操作成功');

      this.$nextTick( () => {
        this._initScroll()
      })
      this.$emit("navType","5")

    }
  }
</script>





