<template>
  <div id="app">
    <div class="header">
      <router-link to="/Home" active-class="active">Home</router-link>
      <router-link to="/Page2" active-class="active">page2</router-link>
      <router-link to="/Swiper" active-class="active">swiper</router-link>
      <router-link to="/Props" active-class="active">Props</router-link>
      <router-link to="/Mint" active-class="active">mint-ui</router-link>
    </div>

    <router-view @navType="navType"></router-view>


    <div style="color: red; position: absolute; width: 100%;background: blueviolet; bottom: 0px; line-height: 40px; color: #fff">页脚</div>
  </div>
</template>

<script>

  export default {
    name: 'app',
    data(){
      return{
        navClassType:1
      }
    },
    methods:{
      navType(type){
        this.navClassType = type
        console.log(type)
      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    position: relative; height: 100%;
  }
  .body{ position: relative; height: 100%}
  .header{ color: red; font-weight: bold; position: absolute; width: 100%; top: 20px; z-index: 999;}
  .router-link-exact-active.active{
    color: red;}
</style>



