<template>
  <div class="body" ref="contentWrapper">
    <div class="content-scroll">
      <div class="content">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide banner">slider1</div>
            <div class="swiper-slide banner">slider2</div>
            <div class="swiper-slide banner">slider3</div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>
<!--@import url("../assets/swiper-3.4.2.min.css");-->
<style type="text/css" scoped>
  @import url("https://cdn.bootcss.com/Swiper/4.0.1/css/swiper.min.css");
  .swiper-slide.banner{ height: 100px; line-height: 100px; background-color: grey;}

</style>
<script>
  import BScroll from 'better-scroll'
  import $ from 'jquery'
  let mySwiper
  import Swiper from '../../static/js/swiper.min.js'
  export default {
    mounted(){
      mySwiper = new Swiper('.swiper-container', {
        autoplay: 5000,//可选选项，自动滑动
      })
    },
    methods: {
      GetData: function () {
        var that = this;
      },
      _initScroll () {
        this.typeScroll = new BScroll(this.$refs.contentWrapper, {
          click: true,
          probeType: 3
        })
      }
    },
    created: function () {
      this.$nextTick( () => {
        this._initScroll()
      })
      this.$emit("navType","3")
      this.GetData();



    }
  }
</script>





